import React, { Component } from 'react';
import Switch from '@material-ui/core/Switch';


function PigletsWs3Population(props) {
  const { pigletsData } = props
  return(
    <table>
      <tbody>
        {pigletsData.count_piglets_0_7 && 
          <tr className='mb-0 font-12'>
            <td className='font-600 pr-3 border-0'>{pigletsData.count_piglets_0_7}</td>
            <td className='border-0'> 0-7  </td>
          </tr>
        }
        {pigletsData.count_piglets_8_14 && 
          <tr className='mb-0 font-12'>
            <td className='font-600 pr-3 border-0'>{pigletsData.count_piglets_8_14}</td>
            <td className='border-0'>8-14 </td>
          </tr>
        }
        {pigletsData.count_piglets_15_21 && 
          <tr className='mb-0 font-12'>
            <td className='font-600 pr-3 border-0'>{pigletsData.count_piglets_15_21}</td>
            <td className='border-0'>15-21 </td>
          </tr>
        }
        {pigletsData.count_piglets_22_28 && 
          <tr className='mb-0 font-12'>
            <td className='font-600 pr-3 border-0'>{pigletsData.count_piglets_22_28}</td>
            <td className='border-0'>22-28 </td>
          </tr>
        }
        {pigletsData.count_piglets_28_plus && 
          <tr className='mb-0 font-12'>
            <td className='font-600 pr-3 border-0'>{pigletsData.count_piglets_28_plus}</td>
            <td className='border-0'>29+ </td>
          </tr>
        }
      </tbody>
    </table>
  )
}

class WSPopulationAndCellOpsComponent extends Component {
  constructor(props) {
    super(props);
    this.state = {
      startDate: null,
      endDate: null,
    }
    this.setData = this.setData.bind(this);
	}

  componentDidMount() {
    this.props.getWsPopulation()
  }

  setData (e) {
    this.setState({
      ...this.state,
      [e.target.name]: e.target.value
    })
  }

  render() {
    const { populationData } = this.props
    return (
      <div className='mt-1'>
        {populationData && populationData['ws'] &&
          <div>
            <h4 className='text-center'>   : {populationData['ws'].sows_count + populationData['ws'].pigs_count}</h4>
            <div className='row mb-0'>
              <div className='col-6 px-0'>
                <div className='card card-style mb-2 rounded-0'>
                  <div className='content my-1'>
                    <p className='mb-0 font-20 text-center'>{populationData['ws'].sows_count} </p>
                    <p className='mb-0 font-16 text-center'>{populationData['ws'].sows_sup_count} </p>
                    <p className='mb-0 font-16 text-center'>
                      {populationData['ws'].sows_count - populationData['ws'].sows_sup_count} 
                    </p>
                  </div>
                </div>
              </div>
              <div className='col-6 px-0'>
                <div className='card card-style mb-2 rounded-0'>
                  <div className='content my-1' style={{"line-height": '20px'}}>
                    <p className='mb-0 font-16 text-center'>
                      : {populationData['ws'].pigs_count}
                      
                      <span className='mb-0 font-13 text-center pl-3'>
                        : {populationData['ws'].gilts_count}
                      </span>
                    </p>
                    <div className='ml-4'>
                      <PigletsWs3Population pigletsData={populationData['ws']}/>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className='row'>
              {populationData['sections'].map(section => 
                <div className='col-2 border border-dark bg-white ' style={{"line-height": '18px'}}>
                  <p className='my-0 font-600 text-center'> {section.section_number}</p>
                  <p className='my-0 font-11'> 
                    <span className='font-600 ml-1'>{section.sows_count}</span>
                  </p>
                  <p className='my-0 font-10 float-left mr-2'> {section.sows_sup_count}</p>
                  <p className='my-0 font-10'> {section.sows_count - section.sows_sup_count}</p>
                  <div className='divider mb-1 bg-mainDark-light'></div>
                  <p className='my-0 font-11'> 
                    <span className='font-600 ml-1'>{section.piglets_count ? section.piglets_count : '0'}</span>
                  </p>
                  {section.gilts_count > 0 &&
                    <p className='my-0 font-11'> {section.gilts_count}</p>
                  }
                  <PigletsWs3Population pigletsData={section}/>
                </div>
                )}
            </div>
          </div>
        }
      </div>
    );
  }
}


export default WSPopulationAndCellOpsComponent


function SectionPopulation (props) {
  const { section } = props

  const ages = (label) => {
    return `
      ${label.split('_')[2]} 
      ${label.split('_')[3] !== 'plus' ? '-' : ''}
      ${label.split('_')[3] === 'plus' ? '+' : label.split('_')[3]} `
  }

  return (
    <div className='border border-dark mt-2 px-3 py-1 rounded-s' style={{width: '150px'}} >
      <p className='mb-0 font-500 d-flex justify-content-between'>
        <span> {section.section_number}</span>
        <span>{section.count_full ? section.count_full : 0}/{section.count_all}</span>
      </p>
      {Object.entries(section).map(item => item[1] && item[0].includes('count_p') &&
        <p className='mb-0 d-flex justify-content-between'>
          <span className='font-700'>{item[1]}</span>
          <span>{ages(item[0])}</span>
        </p>
        )}
    </div>
  )
}

export class WSPopulationPigletsComponent extends Component {
  constructor(props) {
    super(props);
    this.state = {
      editAges: false,
      ageIntervalsState: [
        [null, null], [null, null], [null, null], [null, null], [null, null]
      ]
    }
    this.handleAge = this.handleAge.bind(this);
    this.changeAges = this.changeAges.bind(this);
	}

  componentDidMount() {
    let data = {
      ws_number: this.props.wsNumber,
      age_intervals: this.props.ageIntervals,
    }
    this.props.getWsPopulation(data)
    this.setState({
      ...this.state,
      ageIntervalsState: this.props.ageIntervals
    })
  }

  handleAge (e) {
    let { ageIntervalsState } = this.state
    let ages = ageIntervalsState[e.target.dataset.position]
    if (e.target.name === 'from')
      ages[0] = parseInt(e.target.value)
    if (e.target.name === 'to')
      ages[1] = parseInt(e.target.value)

    ageIntervalsState[e.target.dataset.position] = ages
    this.setState({
      ...this.state,
      ageIntervalsState: ageIntervalsState
      })
  }

  changeAges () {
    let data = {
      ws_number: this.props.wsNumber,
      age_intervals: this.state.ageIntervalsState,
    }
    this.props.getWsPopulation(data)
  }

  render() {
    const { populationData, ageIntervals } = this.props
    const { ageIntervalsState } = this.state
    return (
      populationData &&
        <div className='card card-style mx-0 mb-1'>
          <div className='content mb-0 d-flex justify-content-between'>
            <h5 className='pl-4'>
                : {populationData['ws'] && populationData['ws']['pigs_count']}
            </h5>
            <span className='pr-4' 
              onClick={() => this.setState({...this.state, editAges: !this.state.editAges})}>
               
              <i className="fas fa-cog pl-1"></i>
            </span>
          </div>
          {this.state.editAges && 
            <div className='content my-0 '>
              {[0, 1, 2, 3, 4].map(position =>
                <div className='my-1 d-flex justify-content-center'>
                  <span></span>
                  <input type='number' name='from' data-position={position} onChange={this.handleAge}
                    value={ageIntervalsState[position][0]} className='mx-1'
                    />
                  <span></span>
                  <input type='number' name='to' data-position={position} onChange={this.handleAge}
                    value={ageIntervalsState[position][1]} className='mx-1'/>
                </div>
                )}
                <button onClick={this.changeAges} className='btn rounded bg-mainDark-dark ml-4 mt-2'>
                  
                </button>
                <div className='divider divider-margins mt-3'></div>
            </div>
          }
          <div className='content d-flex flex-wrap mx-1 justify-content-around mt-0'>
            {populationData.sections && populationData.sections.map(section =>
              <SectionPopulation section={section} ageIntervals={ageIntervals}/>
              )}
          </div>
        </div>
    );
  }
}

function OtkormTourTr (props) {
  const { tour, wsNumber } = props
  const measureClass = 'font-11'
  const valueClass = 'mb-0 color-mainDark-dark font-13 font-470'
  const redValueClass = 'mb-0 color-red1-light font-13 font-500'

  return (
    <tr className='text-nowrap'>
      <td>{tour.week_number}. {tour.year}</td>
      <td>
        <p className={valueClass}>
          {tour[`week_weight_qnty_8_${wsNumber}`]}
          <span className={measureClass}> </span>
        </p>
        <span className='font-italic'>{tour[`week_weight_avg_8_${wsNumber}`].toFixed(2)} </span>
      </td>
      <td>
        {tour[`ws${wsNumber}_spec_quantity`] &&
          <p className={valueClass}>{tour[`ws${wsNumber}_spec_quantity`]}
            <span className={measureClass}> </span>
          </p>}
        {tour[`ws${wsNumber}_spec_avg_weight`] > 0 &&
          <span className='font-italic'>
            {tour[`ws${wsNumber}_spec_avg_weight`].toFixed(2)} </span>}       
      </td>
      <td>
        <p className={valueClass}>
          {tour[`prives_without_remont_${wsNumber}`] 
            ? tour[`prives_without_remont_${wsNumber}`].toFixed(2)
            : tour[`prives_${wsNumber}`] > 0 && tour[`prives_${wsNumber}`].toFixed(2)}
        </p>
        {(tour[`prives_without_remont_${wsNumber}`] || tour[`prives_${wsNumber}`] )
          && <span className={measureClass}>/</span>}
      </td>
      <td>
        <p className={valueClass}>
          {tour[`prives_without_remont_1g_${wsNumber}`] 
            ? tour[`prives_without_remont_1g_${wsNumber}`].toFixed(2)
            : tour[`prives_1g_${wsNumber}`] > 0 && tour[`prives_1g_${wsNumber}`].toFixed(2)
          }
        </p>
        {(tour[`prives_without_remont_1g_${wsNumber}`] || tour[`prives_1g_${wsNumber}`])
          && <span className={measureClass}>/</span>}
      </td>
      <td>
        {tour[`ws${wsNumber}_padej_quantity`] &&
          <p className={valueClass}>
            {tour[`ws${wsNumber}_padej_quantity`]}
            <span className={measureClass}> </span>
          </p>}

          {tour[`ws${wsNumber}_padej_percentage`] &&
           <p className={redValueClass}>
             {tour[`ws${wsNumber}_padej_percentage`].toFixed(2) + '%'}
            </p>}
      </td>
      <td>
        {tour[`ws${wsNumber}_vinuzhd_quantity`] &&
          <p className={valueClass}>
            {tour[`ws${wsNumber}_vinuzhd_quantity`]}
            <span className={measureClass}> </span>
          </p>}

          {tour[`ws${wsNumber}_vinuzhd_percentage`] &&
           <p className={redValueClass}>
             {tour[`ws${wsNumber}_vinuzhd_percentage`].toFixed(2) + '%'}
            </p>}
      </td>
      <td>
        {tour[`spec_sv_avg_age_ws${wsNumber}`] &&
          [<p className={valueClass}>{tour[`spec_sv_avg_age_ws${wsNumber}`].toFixed(2)}</p>,
            <span className={measureClass}></span>]}
      </td>
      <td>
        {tour['count_remont_total'] &&
          [
            <p className={valueClass}>{tour['count_remont_total']}</p>,
            <span className={measureClass}> </span>
          ]
        }
      </td>
    </tr>
)
}

function DorosTourTr (props) {
  const { tour, wsNumber } = props
  let labelIn, labelOut = 0
  if (wsNumber === 4) {
    labelIn = 3
    labelOut = 8
  } else {
    labelIn = 4
    labelOut = 8
  }
  const measureClass = 'font-11'
  const valueClass = 'mb-0 color-mainDark-dark font-13 font-470'
  const redValueClass = 'mb-0 color-red1-light font-13 font-500'

  return (
    <tr className='text-nowrap'>
      <td>{tour.week_number}. {tour.year}</td>
      <td>
        <p className={valueClass}>
          {tour[`week_weight_qnty_${labelIn}_${wsNumber}`]}
          <span className={measureClass}> </span>
        </p>
        <span className='font-italic'>{tour[`week_weight_avg_${labelIn}_${wsNumber}`].toFixed(2)} </span>
      </td>
      {wsNumber === 4
        ? <td>
            {tour[`week_weight_qnty_${wsNumber}_${labelOut}`] > 0 &&
              <p className={valueClass}>
                {tour[`week_weight_qnty_${wsNumber}_${labelOut}`]}
                <span className={measureClass}> </span>
              </p>}
              {tour[`week_weight_qnty_${wsNumber}_${labelOut}`] > 0 &&
                <span className='font-italic'>
                  {tour[`week_weight_avg_${wsNumber}_${labelOut}`].toFixed(2)} </span>}
          </td>
        : <td>
            {tour[`week_weight_qnty_ws${wsNumber}`] > 0 &&
              <p className={valueClass}>
                {tour[`week_weight_qnty_ws${wsNumber}`]}
                <span className={measureClass}> </span>
              </p>}
              {tour[`week_weight_avg_ws${wsNumber}`] > 0 &&
                <span className='font-italic'>
                  {tour[`week_weight_avg_ws${wsNumber}`].toFixed(2)} </span>}
          </td>
      }
      <td>
        <p className={valueClass}>
          {tour[`prives_without_remont_${wsNumber}`] 
            ? tour[`prives_without_remont_${wsNumber}`].toFixed(2)
            : tour[`prives_${wsNumber}`] > 0 && tour[`prives_${wsNumber}`].toFixed(2)}
        </p>
        {(tour[`prives_without_remont_${wsNumber}`] || tour[`prives_${wsNumber}`] )
          && <span className={measureClass}>/</span>}
      </td>
      <td>
        <p className={valueClass}>
          {tour[`prives_without_remont_1g_${wsNumber}`] 
            ? tour[`prives_without_remont_1g_${wsNumber}`].toFixed(2)
            : tour[`prives_1g_${wsNumber}`] > 0 && tour[`prives_1g_${wsNumber}`].toFixed(2)
          }
        </p>
        {(tour[`prives_without_remont_1g_${wsNumber}`] || tour[`prives_1g_${wsNumber}`])
          && <span className={measureClass}>/</span>}
      </td>
      <td>
        {tour[`ws${wsNumber}_padej_quantity`] &&
          <p className={valueClass}>
            {tour[`ws${wsNumber}_padej_quantity`]}
            <span className={measureClass}> </span>
          </p>}

          {tour[`ws${wsNumber}_padej_percentage`] &&
           <p className={redValueClass}>
             {tour[`ws${wsNumber}_padej_percentage`].toFixed(2) + '%'}
            </p>}
      </td>
      <td>
        {tour[`ws${wsNumber}_vinuzhd_quantity`] &&
          <p className={valueClass}>
            {tour[`ws${wsNumber}_vinuzhd_quantity`]}
            <span className={measureClass}> </span>
          </p>}

          {tour[`ws${wsNumber}_vinuzhd_percentage`] &&
           <p className={redValueClass}>
             {tour[`ws${wsNumber}_vinuzhd_percentage`].toFixed(2) + '%'}
            </p>}
      </td>
      <td>
        {tour[`ws${wsNumber}_prirezka_quantity`] &&
          <p className={valueClass}>
            {tour[`ws${wsNumber}_prirezka_quantity`]}
            <span className={measureClass}> </span>
          </p>}

          {tour[`ws${wsNumber}_prirezka_percentage`] &&
           <p className={redValueClass}>
             {tour[`ws${wsNumber}_prirezka_percentage`].toFixed(2) + '%'}
            </p>}
      </td>
      {wsNumber === 4 
        ? <td>
            {tour[`sv_age_${wsNumber}_${labelOut}`] &&
              [<p className={valueClass}>
                {tour[`sv_age_${wsNumber}_${labelOut}`] 
                  && tour[`sv_age_${wsNumber}_${labelOut}`].toFixed(2)}
              </p>,
              <span className={measureClass}></span>]
            }
          </td>
        : <td>
            {tour[`sv_age_ws${wsNumber}`] &&
              [<p className={valueClass}>
                {tour[`sv_age_ws${wsNumber}`] 
                  && tour[`sv_age_ws${wsNumber}`].toFixed(2)}
              </p>,
              <span className={measureClass}></span>]
            }
          </td>
      }
    </tr>
)
}

export class WSToursInfoComponent extends Component {
  constructor(props) {
    super(props);
    this.state = {
      showCurrentTours: true
    }
  }
  
  render() {
    const { toursData, wsNumber } = this.props
    return (
      toursData &&
      <div className='card card-style mx-0 mt-0'>
        <div className='content mb-0 mx-1'>
          <div className='d-flex justify-content-between align-items-baseline mb-2 mx-4'>
            <h5 className='my-0'>
              
            </h5>
            <div className='my-0'>
              <label> </label>
              <Switch
                checked={this.state.setShowCurrent}
                onChange={() => this.setState({...this.state, showCurrentTours: !this.state.showCurrentTours})}
                name="showCurrentTours"
              />
            </div>
          </div>
          <table className='table table-sm table-responsive' style={{lineHeight: '15px'}}>
            {[5, 6, 7].includes(wsNumber)
              ? <thead>
                  <th></th>
                  <th>. ,.</th>
                  <th>. ,.</th>
                  <th> .   </th>
                  <th>  .   </th>
                  <th></th>
                  <th>. </th>
                  <th>. . </th>
                  <th>. </th>
                </thead>
              : <thead>
                  <th></th>
                  <th>. ,.</th>
                  <th>. ,.</th>
                  <th> .   </th>
                  <th>  .   </th>
                  <th></th>
                  <th>. </th>
                  <th></th>
                  <th>. . </th>
                </thead>
            }
            <tbody>
            {[5, 6, 7].includes(wsNumber)
              ? toursData.length > 0 && toursData.map(tour => 
                this.state.showCurrentTours
                  ? <OtkormTourTr tour={tour} wsNumber={wsNumber} />
                  : tour[`prives_${wsNumber}`] > 0 && <OtkormTourTr tour={tour} wsNumber={wsNumber} />)
              : toursData.length > 0 && toursData.map(tour => 
                this.state.showCurrentTours
                  ? <DorosTourTr tour={tour} wsNumber={wsNumber} />
                  : tour[`prives_${wsNumber}`] > 0 && <DorosTourTr tour={tour} wsNumber={wsNumber} />)
            }  
            </tbody>
          </table>
        </div>
      </div>
    );
  }
}